.footer {
  background: #292d33;
  display: flex;
  color: #e5e7e8;
  border-top: 1px solid silver; }

.footer__contacts {
  display: flex;
  flex-direction: column;
  padding-bottom: 10px; }

.footer__Title {
  text-align: center;
  font-weight: bold;
  padding-bottom: 30px; }

.footer__buttons {
  display: flex;
  padding: 20px 0; }

.footer__inner {
  display: flex;
  flex-direction: column; }

.footer__inner input {
  margin-bottom: 10px;
  padding: 10px 15px;
  font-size: 16px;
  background: #e5e7e8;
  line-height: 24px;
  border: #cd1717;
  border-radius: 8px;
  letter-spacing: -0.2px;
  outline: none;
  resize: none; }

.footer__submit {
